import Vue from 'vue'
import Vuex from 'vuex'
// import axios from 'axios'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    count: 100,
    list: [],
    inputValue: '',
    nId: 0,
    keyValue: 'all'
  },
  mutations: {
    initData(state, data) {
      state.list = data
    },
    setInputValue(state, data) {
      state.inputValue = data
    },
    addTodoItem(state) {
      const item = {
        id: state.nId,
        info: state.inputValue.trim(),
        done: false
      }
      state.list.push(item)
      window.localStorage.setItem('list', JSON.stringify(state.list))
      state.nId++
      state.inputValue = ''
    },
    removeTodoItem(state, id) {
      // 根据传过来的id值进行查找，有则返回该数组的索引，没有则返回-1
      const i = state.list.findIndex(x => x.id === id)
      // 删除该索引的值
      state.list.splice(i, 1)
      window.localStorage.setItem('list', JSON.stringify(state.list))
    },
    itemChangeStatus(state, param) {
      // 根据传过来的id值进行查找索引，再根据索引修改内容
      const i = state.list.findIndex(x => x.id === param.id)
      if (i !== -1) {
        state.list[i].done = param.status
      }
      window.localStorage.setItem('list', JSON.stringify(state.list))
    },
    cleanDone(state) {
      state.list = state.list.filter(x => x.done === false)
      window.localStorage.setItem('list', JSON.stringify(state.list))
    },
    changeValueKey(state, key) {
      state.keyValue = key
    }
  },
  actions: {
    getList(context) {
      const data = JSON.parse(window.localStorage.getItem('list'))
      console.log(data)
      if (data === null) {
        window.localStorage.setItem('list', JSON.stringify([]))
        // context.commit('initData', data)
      } else (context.commit('initData', data))
    }
  },
  getters: {
    unDone(state) {
      if (state.list) {
        // 对数组中未完成事件进行筛选，并返回条数，不影响原有state中的数据
        return state.list.filter(x => x.done === false).length
      }
    },
    filterItem(state) {
      if (state.keyValue === 'all') {
        return state.list
      }
      if (state.keyValue === 'done') {
        return state.list.filter(x => x.done === true)
      }
      if (state.keyValue === 'undone') {
        return state.list.filter(x => x.done === false)
      }
    }
  },
  modules: {
  }
})
