<template>
  <div id="app">
    <div class="wrapper">
      <div class='title'>todolist</div>
      <div class="header">
        <a-input
          :value="inputValue"
          @change="inputChange"
          placeholder="今日计划安排"
          :style="{ height: '40px' , width:'585px'}"
        />
        <a-button
          type="primary"
          @click="addTodoList"
          :style="{ height: '40px' , width:'100px', marginLeft:'10px'}"
        >添加事项</a-button>
      </div>
      <a-list bordered :data-source="filterItem">
        <a-list-item slot="renderItem" slot-scope="item">
          <!-- 复选框 -->
          <a-checkbox :checked="item.done" @change="(e)=>{itemStatusChange(e, item.id)}">
            <span :class="{remove:item.done}">{{ item.info }}</span>
          </a-checkbox>
          <a-button type="link" @click="removeItem(item.id)">删除</a-button>
        </a-list-item>
        <div slot="footer" class="footer">
          <span :style="{ lineHeight:'32px' }">{{unDone}}条剩余</span>
          <div>
            <a-button
              :type="keyValue === 'all' ? 'primary': 'default'"
              @click="changeKeyValue('all')"
            >全部</a-button>
            <a-button
              :type="keyValue === 'done' ? 'primary': 'default'"
              @click="changeKeyValue('done')"
            >已完成</a-button>
            <a-button
              :type="keyValue === 'undone' ? 'primary': 'default'"
              @click="changeKeyValue('undone')"
            >未完成</a-button>
          </div>
          <div>
            <a-button type="link" @click="clean">清除已完成事项</a-button>
          </div>
        </div>
      </a-list>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
export default {
  data() {
    return {}
  },
  created() {
    this.$store.dispatch('getList')
  },
  computed: {
    ...mapState(['list', 'inputValue', 'keyValue']),
    ...mapGetters(['unDone', 'filterItem'])
  },
  methods: {
    inputChange(e) {
      this.$store.commit('setInputValue', e.target.value)
    },
    addTodoList() {
      // 判断输入框内容是否合法
      if (!this.inputValue.trim()) {
        return this.$message.warning('请输入今日待办事件')
      }
      this.$store.commit('addTodoItem')
    },
    // 不推荐直接删除映射的list，而是通过mutation中的事件进行删除
    removeItem(id) {
      this.$store.commit('removeTodoItem', id)
    },
    itemStatusChange(e, id) {
      // console.log(e.target.checked)
      const obj = {
        id: id,
        status: e.target.checked
      }
      this.$store.commit('itemChangeStatus', obj)
    },
    clean() {
      this.$store.commit('cleanDone')
    },
    changeKeyValue(key) {
      this.$store.commit('changeValueKey', key)
    }
  }
}
</script>

<style>
.wrapper {
  width: 700px;
  position: absolute;
  left: 50%;
  top: 50px;
  transform: translate(-50%);
}
.header {
  margin-bottom: 20px;
}
.footer {
  display: flex;
  justify-content: space-between;
}

.remove {
  text-decoration: line-through;
  color: #d3d3d3;
}
.title {
  font-size: 50px;
  font-family: 'SingleMalta';
  transform: translate(30%);
}
</style>
